import React, { useState } from "react";
import axios from "axios";
import "./App.css";
import 'katex/dist/katex.min.css';
import { BlockMath } from 'react-katex';
import SolutionStep from './components/solutionStep.js';

const styles = {
  container: {
    maxWidth: '800px',
    margin: '0 auto',
    padding: '20px',
    fontFamily: 'Arial, sans-serif',
  },
  title: {
    fontSize: '24px',
    fontWeight: 'bold',
    marginBottom: '20px',
  },
  form: {
    marginBottom: '20px',
  },
  input: {
    width: '70%',
    padding: '10px',
    fontSize: '16px',
    marginRight: '10px',
  },
  button: {
    padding: '10px 20px',
    fontSize: '16px',
    backgroundColor: '#4CAF50',
    color: 'white',
    border: 'none',
    cursor: 'pointer',
  },
  finalAnswer: {
    backgroundColor: '#e6f4ea',
    padding: '15px',
    borderRadius: '8px',
  },
  finalAnswerText: {
    fontSize: '18px',
    fontWeight: 'bold',
    margin: 0,
  },
};

const CalculusSolutionDisplay = ({ solution }) => {
  if (!solution) {
    return <div>No solution available</div>;
  }

  return (
    <div>
      {solution.solution.map((item, index) => (
        <SolutionStep
          key={index}
          step={item.step}
          action={item.action}
          equation={item.equation}
          explanation={item.explanation}
        />
      ))}
      <div style={styles.finalAnswer}>
        <h3 style={styles.finalAnswerText}>Final Answer:</h3>
        <BlockMath math={solution.final_answer} />
      </div>
    </div>
  );
};

function App() {
  const [equation, setEquation] = useState("f(x) = x^2");
  const [solution, setSolution] = useState(null);

  const handleSolve = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post("http://localhost:3000/api/solve", {
        equation,
      });
      console.log("Raw response:", response.data);

      // Parse the JSON string in the response
      const parsedSolution = JSON.parse(response.data);
      setSolution(parsedSolution);
    } catch (error) {
      console.error("Error:", error);
      setSolution(null);
    }
  };

  return (
    <div style={styles.container}>
      <h1 style={styles.title}>AlgoVista Math Solver</h1>
      <form onSubmit={handleSolve} style={styles.form}>
        <input
          type="text"
          value={equation}
          onChange={(e) => setEquation(e.target.value)}
          placeholder="Enter a math equation"
          style={styles.input}
        />
        <button type="submit" style={styles.button}>Solve</button>
      </form>
      {solution && <CalculusSolutionDisplay solution={solution} />}
    </div>
  );
}

export default App;